import * as olTilegrid from "ol/tilegrid";
import TileGrid from "ol/tilegrid/TileGrid";
import * as olProj from "ol/proj";
import XYZ from "ol/source/XYZ";

export default class LimitedExtentXYZ extends XYZ {
  constructor(options) {
    const tileGrid = olTilegrid.createXYZ({
      extent: options.projection
        ? olProj.get(options.projection).getExtent()
        : undefined,
      tileSize: options.tileSize,
      minZoom: options.minZoom,
      maxZoom: options.maxZoom,
      maxResolution: options.maxResolution
    });
    const baseOptions = {
      tileGrid: new TileGrid({
        extent: options.extent,
        origin: tileGrid.getOrigin(0),
        resolutions: tileGrid.getResolutions(),
        tileSize: tileGrid.getTileSize(0),
        minZoom: tileGrid.getMinZoom()
      }),
      ...options
    };
    delete baseOptions.extent;
    super(baseOptions);
  }
}
